import React, { useEffect, useState } from "react";
import "./User.css";
import axios from "axios";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import ApiClient, { api_method } from "../ApiClient";
import { ConstructionOutlined } from "@mui/icons-material";

function User() {
  const [userName, setUserName] = useState("");
  const [userName2, setUserName2] = useState("");
  const [userEmailAddress, setUserEmailAddress] = useState("");
  const [userEmailAddress2, setUserEmailAddress2] = useState("");
  const [userID, setUserID] = useState("");
  const [userID2, setUserID2] = useState("");
  const [passWord, setPassWord] = useState("");
  const [passWord2, setPassWord2] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [phoneNumber2, setPhoneNumber2] = useState("");
  const [Image, setImage] = useState(
    "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
  );
  const [smShow, setSmShow] = useState(false);

  const token = sessionStorage.getItem("authorizeKey");

  const userInfo = async () => {
    try {
      const response = await axios.get(
        "http://api.hillntoe.com:7810/api/account/info",
        {
          headers: {
            Authorization: token,
          },
        }
      );
      if (response?.status === 200) {
        const username = response.data.userName;
        const userEmail = response.data.mailAddress;
        const userid = response.data.userID;
        const phonenumber = response.data.phoneNumber;
        //    
        // const passWord = response.data.password;
        setUserName(username);
        setUserEmailAddress(userEmail);
        setUserID(userid);
        setPhoneNumber(phonenumber);
        // setPassWord(passWord);
      } else {
        throw new Error(`Failed to fetch device info(${response?.status})`);
      }
    } catch (error) {
      console.error("Error fetching device info:", error);
    }
  };


  

const handleChangeInfo = async () => {
  try {
    const data = {
      userID: userID,
      password: passWord2, 
      userName: userName2,
      mailAddress: userEmailAddress2,
      phoneNumber: phoneNumber2,
    };

    const client = new ApiClient();

    // Send a POST request using ApiClient
    const response = await client.RequestAsync(
      api_method.post,
      "/api/account/modify",
      null,
      JSON.stringify(data),
      token
    );

    if (response?.status === 200) {
      console.log("Info modified successfully.");
    } else {
      throw new Error(`Failed to fetch device info (${response})`);
    }
  } catch (error) {
    console.error("Error", error);
  }
};

  const handleWithdrawal = async () => {
    try {
      const data = {
        UserID: userID2,
        Password: passWord2,
      };

      const client = new ApiClient();
      const response = await client.RequestAsync(
        api_method.post,
        "/web/account/withdrawal",
        null,
        data,
        token
      );
      if (response?.status === 200) {
      } else {
        alert(" ");
      }
    } catch (error) {
      console.error(`: ${error}`);
    }
  };

  useEffect(() => {
    userInfo();
  }
  , []);


  return (
    <div className="User-Main">
      {/* w: 900px h:830px */}
      <div className="User-Content">
        {/*   */}
        <div className="User-Content-Profile">
          <div className="Profile-header">
            <span className="User-content-title"> </span>
          </div>
          <div className="Profile-body">
            <div className="Profile-body-UserIcon">
              <h1 className="userAccountIcon">
                <img
                  src={Image}
                  style={{ width: "100px" }}
                  alt=" "
                />
              </h1>
              <div className="userAccount-Name"> {userName}</div>
              <div>{userEmailAddress}</div>
            </div>
            <div className="Profile-body-UserInfo">
              <div className="body-userInfo">
                <span style={{ marginRight: "136px" }}></span>
                <span>{userName}</span>
              </div>
              <hr />
              <div className="body-userInfo">
                <span style={{ marginRight: "116px" }}></span>
                <span>{userID}</span>
              </div>
              <hr />
              <div className="body-userInfo">
                <span style={{ marginRight: "116px" }}></span>
                <span>{userEmailAddress}</span>
              </div>
              <hr />
              <div className="body-userInfo">
                <span style={{ marginRight: "116px" }}></span>
                <span>01096868946</span>
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="User-Content-Modify">
          <div className="Modify-header">
            <span className="User-content-title">  </span>
          </div>

          <div className="Modify-Member">
            <div className="UserName-Modify">
              <span></span>
              <input
                type="text"
                className="Modify-input"
                style={{ marginLeft: "159px" }}
                placeholder=" "
                value={userID2}
                onChange={(e) => {
                  setUserID2(e.target.value);
                }}
              />
              <button onClick={handleChangeInfo}> </button>
            </div>
            <div className="UserName-Modify">
              <span></span>
              <input
                type="text"
                className="Modify-input"
                style={{ marginLeft: "145px" }}
                placeholder=" "
                value={userEmailAddress2}
                onChange={(e) => {
                  setUserEmailAddress2(e.target.value);
                }}
              />
              <button onClick={handleChangeInfo}> </button>
            </div>
            <div className="UserName-Modify">
              <span></span>
              <input
                type="text"
                className="Modify-input"
                style={{ marginLeft: "145px" }}
                placeholder="  "
                value={phoneNumber2}
                onChange={(e) => setPhoneNumber2(e.target.value)}
              />
              <button onClick={handleChangeInfo}>  </button>
            </div>
            <div className="UserName-Modify">
              <span></span>
              <input
                type="password"
                className="Modify-input"
                style={{ marginLeft: "131px" }}
                placeholder=" "
                value={passWord2}
                onChange={(e) => {
                  setPassWord2(e.target.value);
                }}
              />
              <button> </button>
            </div>
            <div className="UserName-Modify">
              <span> </span>
              <input
                type="password"
                className="Modify-input"
                style={{ marginLeft: "113px" }}
                placeholder="  "
              />
            </div>
            <div className="UserName-Modify">
              <span>  </span>
              <input
                type="password"
                className="Modify-input"
                style={{ marginLeft: "76px" }}
                placeholder="  "
              />
            </div>
          </div>
        </div>
      </div>
      <div className="account-Withdrawal">
             
        <button
          onClick={() => {
            setSmShow(true);
          }}
          className="me-2"
        >
          
        </button>
      </div>

      {/*    */}

      <Modal size="sm" show={smShow} onHide={() => setSmShow(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div>
            <label> </label>
            <input
              type="text"
              value={userID2}
              onChange={(e) => setUserID2(e.target.value)} //   
            />
          </div>
          <div>
            <label></label>
            <input
              type="password"
              value={passWord2}
              onChange={(e) => setPassWord2(e.target.value)} //  
            />
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setSmShow(false)}>
            
          </Button>
          <Button variant="danger" onClick={handleWithdrawal}>
             
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default User;
