import React, { useState } from "react";
import ApiClient, { api_method } from "../ApiClient";
import "./SignUp.css";
import { useNavigate,Routes,Route,Link } from "react-router-dom";
const SignUp = () => {
  const [userID, setUserID] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState(""); //    
  const [userName, setUserName] = useState("");
  const [mailAddress, setMailAddress] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [errorMessages, setErrorMessages] = useState({}); //     

  const naviage = useNavigate()

  const handleSignUpSubmit = async (e) => {
    e.preventDefault();

    const newErrorMessages = {};

    if (!userID) {
      newErrorMessages.userID = " .";
    }

    if (!password) {
      newErrorMessages.password = " .";
    }

    if (password !== confirmPassword) {
      newErrorMessages.confirmPassword = "  .";
    }

    //     

    setErrorMessages(newErrorMessages);
    if (Object.keys(newErrorMessages).length === 0) {
      try {
        const data = {
          UserID: userID,
          Password: password,
          UserName: userName,
          MailAddress: mailAddress,
          PhoneNumber: phoneNumber,
          Right:0,
          isEnable:0
        };
        const client = new ApiClient();
        const response = await client.RequestAsync(
          api_method.post,
          "/web/account/signup",
          null,
          data
        );

        if (response?.status === 200) {
          console.log(" !");
          alert(" ");
          setUserID("");
          setPassword("");
          setConfirmPassword(""); //     
          setUserName("");
          setMailAddress("");
          setPhoneNumber("");
        } else {
          console.error(" ");
          alert(" ");
        }
      } catch (error) {
        console.error(`: ${error}`);
      }
    }
  };

  return (
    <div className="Signup-container">
      <div className="Signup-Header-container">
      </div>
      <div className="Signup-Main-container">
        <span className="Signup-title"></span>
        <form className="Signup-form-input" onSubmit={handleSignUpSubmit}>
          <div></div>
          <div className="input-title-1">
            <div className="title-title"></div>
            <input
              type="text"
              value={userID}
              onChange={(e) => setUserID(e.target.value)}
              placeholder=" "
              className="Signup-input"
            />
          </div>
          <div className="input-title">
            <div className="title-title"></div>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=" "
              className="Signup-input"
            />
          </div>
          <div className="input-title">
            <div className="title-title"> </div>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="   "
              className="Signup-input"
            />
            <div className="error-message">{errorMessages.confirmPassword}</div>
          </div>
          <div className="input-title">
            <div></div>
            <input
              type="text"
              value={userName}
              onChange={(e) => setUserName(e.target.value)}
              placeholder=" "
              className="Signup-input"
            />
          </div>
          <div className="input-title">
            <div className="title-title"></div>
            <input
              type="email"
              value={mailAddress}
              onChange={(e) => setMailAddress(e.target.value)}
              placeholder="  "
              className="Signup-input"
            />
          </div>
          <div className="input-title">
            <div className="title-title"></div>
            <input
              type="tel"
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              placeholder=" "
              className="Signup-input"
            />
          </div>
          <div className="input-title">
            <button type="submit" className="Signup-Btn">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SignUp;
