import React, { useState, useEffect, useId } from "react";
import { Link } from "react-router-dom";
import "./Login.css";
import ApiClient, { api_method } from "./ApiClient";
import MainLayout from "./MainLayout";
import Modal from "react-bootstrap/Modal";
import { useNavigate, Routes, Route } from "react-router-dom";
import mainlogo from "../asset/logo_pwc.png"
import axios from "axios";



const Login = () => {
  const storedToken = sessionStorage.getItem("authorizeKey");
  const [isRemember, setIsRemember] = useState(false); //ID Remember
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loggedIn, setLoggedIn] = useState(storedToken !== null);
  const [loginError, setLoginError] = useState(false);
  const [show, setShow] = useState(false);
  const navigate = useNavigate();

  const handleClose = () => {
    setShow(false);
    navigate("/");
  };
  const handleShow = () => setShow(true);

  useEffect(() => {
    if (storedToken !== null) {
      checkAuthToken(storedToken);
    }
  }, []);

  const checkAuthToken = async (token) => {
    const client = new ApiClient();
    const response = await client.RequestAsync(
      api_method.get,
      "/api/account/info",
      null,
      null,
      token
    );
    if (response?.status === 200) {
      setLoggedIn(true);
    } else {
      setLoggedIn(false);
      sessionStorage.removeItem("authorizeKey");
    }
  };

  const handleLoginSubmit = async (e) => {
    e.preventDefault();

    try {
      const data = {
        Id: username,
        Password: password,
      };

      const client = new ApiClient();
      const response = await client.RequestAsync(
        api_method.post,
        "/web/login",
        null,
        data
      );

      if (response?.status === 200) {
        const token = "Bearer " + response.data;
        sessionStorage.setItem("authorizeKey", token);
        setLoggedIn(true);
        setLoginError(false);
      } else {
        setLoginError(true);
      }
    } catch (error) {
      console.error(`: ${error}`);
    }
  };

  if (loggedIn) {
    return <MainLayout />;
  }

  return (
    <div className="login-container">
      <Modal  show={show} onHide={handleClose} >
        <Modal.Header
          style={{
            backgroundColor: "#fff",
            height: "70px",
            borderBottom: "1px solid #0041b9",
          }}
        >
          <div style={{ width: "50%", height: "100%", paddingTop: "20px" }}>
            <Link to="/findId" className="find findid">
               
            </Link>
          </div>
          <div style={{ width: "52%", height: "100%", paddingTop: "20px" }}>
            <Link to="/findPw" className="find findpw">
               
            </Link>
          </div>
        </Modal.Header>
        <Routes>
          <Route
            exact
            path="/findId"
            element={<FindID handleClose={handleClose} />}
          />
          <Route
            exact
            path="/findPw"
            element={<FindPW handleClose={handleClose} />}
          />

        </Routes>
      </Modal>
      <img className="hillntoe-symbol" src={mainlogo}/>
      <div className="logo-container">
        <div className="hillntoe-Info" style={{fontSize:'30px',fontWeight:'bold'}}>  SW</div>
        <p className="logo-text">
               IoT, ,
          <br />
               .
        </p>
      </div>
      <h2 className="Login_title"></h2>
      <div className="login-form-container">
        <div className="login-box">
          <form onSubmit={handleLoginSubmit}>
            <div className="form-group">
              <input
                type="text"
                id="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder=" "
                required
              />
            </div>
            <div className="form-group">
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                placeholder=" "
              />
            </div>
            <div className="check_label">
              <input type="checkbox" />
              <label htmlFor="idSaveCheckbox" className="checkbox-label">
                 
              </label>
            </div>
            <button
              type="button"
              className="search_btn"
              onClick={() => {
                navigate("/findid");
                handleShow();
              }}
            >
               
            </button>
            <button type="submit" className="login_btn">
              
            </button>
          </form>
          <button type="button" className="signin_btn">
            <Link
              to="signup"
              style={{
                color: "#0041b9",
                textDecoration: "none",
                width: "100%",
                display:'block'
              }}
            >
              
            </Link>
          </button>
        </div>
      </div>
    </div>
  );
};

function FindID(props) {
  const [mailAddress, setMailAddress] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [foundId, setFoundId] = useState(""); //    

  const navigate = useNavigate();

  const handleFindIdSubmit = () => {
    //         .
    //       .
    axios
      .post("http://192.168.0.78:7810/api/account/find/id", {
        mailAddress: mailAddress,
        phoneNumber: phoneNumber,
      })
      .then((response) => {
        if (response.status === 200) {
          const foundUsername = response.data;
          setFoundId(foundUsername);
        } else {
          setFoundId("   .");
        }
      })
      .catch((error) => {
        setFoundId("   .");
      });
  };

  return (
    <div className="Login-Find-Modal">
      {foundId ? (
        <div>
          <div className="foundIDbox">
            <div className="foundIDContent">
                 .
            </div>
            <div className="foundID">{foundId}</div>
          </div>
          <div className="foundBtn_zip">
            <button className="foundBtn1" onClick={props.handleClose}>
              
            </button>
            <button
              className="foundBtn2"
              onClick={() => {
                navigate("/findpw");
              }}
            >
               
            </button>
          </div>
        </div>
      ) : (
        <>
          <div className="find-id-title">
            
          </div>
          <input
            className="find-id-input"
            type="email"
            value={mailAddress}
            onChange={(e) => setMailAddress(e.target.value)}
            placeholder="  "
          />
          <div style={{ marginTop: "30px" }} className="find-id-title">
            
          </div>
          <input
            className="find-id-input"
            type="tel"
            value={phoneNumber}
            onChange={(e) => setPhoneNumber(e.target.value)}
            placeholder="  "
          />
          <button
            className="find-id-Btn"
            style={{ marginTop: "84px" }}
            onClick={handleFindIdSubmit}
          >
             
          </button>
        </>
      )}
    </div>
  );
}

function FindPW(props) {
  const navigate = useNavigate();

  const [userID, setUserID] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [mailAddress, setMailAddress] = useState("");
  const [foundPw, setFoundPw] = useState("");

  const handleFindPwSubmit = () => {
    axios
      .post("http://192.168.0.78:7810/api/account/find/password", {
        userID: userID,
        phoneNumber: phoneNumber,
        mailAddress: mailAddress,
      })
      .then((response) => {
        if (response.status === 200) {
          setFoundPw(`${mailAddress},   `);
        } else {
          setFoundPw("   ");
        }
      })
      .catch((error) => {
        if (error.response && error.response.status === 400) {
          setFoundPw("   ");
        } else {
          setFoundPw(" .   ");
        }
      });
  };

  return (
    <div  className="Login-Find-Modal">
      {foundPw ? (
        <div>
          <div className="foundIDbox">
            <div className="foundIDContent">
                {mailAddress}  <br />
                .
            </div>
          </div>
          <div className="foundBtn_zip">
            <button className="foundBtn3" onClick={props.handleClose}>
              
            </button>
            <button
              className="foundBtn4"
              onClick={() => {
                navigate("/modify");
              }}
            >
               
            </button>
          </div>
        </div>
      ) : (
        <>
          <div>
            <div className="find-id-title"></div>
            <input
              className="find-id-input"
              type="text"
              value={userID}
              onChange={(e) => setUserID(e.target.value)}
              placeholder=" "
            />
          </div>
          <div>
            <div className="find-id-title"></div>
            <input
              className="find-id-input"
              type="email"
              value={mailAddress}
              onChange={(e) => setMailAddress(e.target.value)}
              placeholder="  "
            />
          </div>
          <div>
            <div className="find-id-title"></div>
            <input
              className="find-id-input"
              type="tel"
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              placeholder="  "
            />
          </div>
          <button className="find-id-Btn" onClick={handleFindPwSubmit}>
             
          </button>
        </>
      )}
    </div>
  );
}



export default Login;
